const load = async ({ locals }) => {
  const { prisma } = locals;
  const openElections = await prisma.election.findMany({
    orderBy: [
      {
        expiresAt: "asc"
      }
    ],
    where: {
      expiresAt: {
        gte: /* @__PURE__ */ new Date()
      }
    },
    select: {
      markdown: true,
      markdownEn: true,
      link: true,
      expiresAt: true,
      committee: true
    }
  });
  return {
    openElections
  };
};
export {
  load
};
